/* ê¸°ë³¸ ë ˆì´ì•„ì›ƒ ì„¤ì • */
.layout {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: #f8f2e5; 
  }
  
  /* í—¤ë” ìŠ¤íƒ€ì¼ */
  .header {
    background-color: #e7dbb4; 
    color: white;
    padding: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  /* ë¡œê³ ì™€ ë²„íŠ¼ ê·¸ë£¹ */
  .logo-group {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  /* ë²„íŠ¼ ê·¸ë£¹ */
  .button-group {
    display: flex;
    gap: 10px;
  }
  
  .button {
    background-color: transparent;
    white-space: nowrap;
    color: #0c0901; 
    font-weight: bold;
    padding: 8px 15px;
    border-radius: 8px;
    text-align: center;
    font-size: 17px;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  .button:hover {
    background-color: transparent;
  }
  
  /* My Page ë²„íŠ¼ */
  .my-page {
    margin-left: 24px;
  }
  
  .my-page-button {
    background: linear-gradient(to right, #c2a31c, #cea842); /* í•‘í¬ìƒ‰ì—ì„œ ë¹¨ê°„ìƒ‰ìœ¼ë¡œ ê·¸ë¼ë””ì–¸íŠ¸ */
    color: white;
    font-weight: bold;
    padding: 10px 20px;
    border-radius: 9999px;
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.1);
    font-size: 18px;
    cursor: pointer;
    transition: all 0.3s ease;
    border: 2px solid white;
  }
  
  .my-page-button:hover {
    background: linear-gradient(to right, #caa914, #dfdd60);
  }
  
  .my-page-button:active {
    background: linear-gradient(to right, #d1ba37, #e4cc8a);
  }
  
  /* ê²€ìƒ‰ ë° íšŒì›ê°€ì…/ë¡œê·¸ì¸ */
  .auth-group {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  
  .search-input {
    padding: 8px 16px;
    border-radius: 8px;
    border: 1px solid #d1d5db;
    color: #9b6f1e;
  }
  
  .auth-button {
    background: transparent;
    white-space: nowrap;
    color: rgb(0, 0, 0);
    font-size: 14px;
    padding: 8px 16px;
    border-radius: 8px;
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  .auth-button:hover {
    background-color: transparent;
  }
  
  /* ë³¸ë¬¸ ì»¨í…ì¸  */
  .main-content {
    flex-grow: 1;
    padding: 10px;
  }

  .recipe-tabs {
    display: flex;
    justify-content: center;
    gap: 10px;
    padding: 10px;
    background-color: #f8f2e5;
  
  }
  
  .tab-button {
    padding: 10px 15px;
    border: none;
    background-color: white;
    color: black;
    cursor: pointer;
    font-size: 16px;
  }
  
  .tab-button:hover {
    background-color: #e0e0e0;
  }
  