.signup-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .signup-box {
    width: 30%;  /* í™”ë©´ì˜ 1/3 ì •ë„ë§Œ ì°¨ì§€ */
    min-width: 320px;  /* ë„ˆë¬´ ì‘ì•„ì§€ëŠ” ê±¸ ë°©ì§€ */
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: white;
    text-align: center;
  }
  
  .signup-box h2 {
    margin-bottom: 15px;
  }
  
  .signup-box input,
  .signup-box select {
    width: 100%;  /* í•œ ì¤„ì„ ê°€ë“ ì±„ìš°ê²Œ ì„¤ì • */
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .row {
    display: flex;
    gap: 10px;
  }
  
  .row select {
    flex: 1;
  }
  
  .password-box {
    position: relative;
  }
  
  .eye-icon {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    font-size: 16px;
  }
  
  button {
    width: 100%;
    padding: 12px;
    background-color: #ff7f50;  /* ì‚°ëœ»í•œ ì½”ë„ ìƒ‰ìƒ */
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #ff6347; /* ë²„íŠ¼ í˜¸ë²„ ì‹œ ë” ì§„í•œ ìƒ‰ */
